import React, { Component } from 'react';
import Card from './card'
import CardsDestination from './cardsDestination'
import { DragDropContext, Droppable } from 'react-beautiful-dnd'

import z from "../cards/zoovu-z.svg"
import o from "../cards/zoovu-o.svg";
import v from "../cards/zoovu-v.svg";
import u from "../cards/zoovu-u.svg";

const cards = {
    cardId: [
        '0', '1', '2', '3', '4'
    ],
    cardImage: [
        z, o, o, v, u
    ],
    correct: [
        '0', '1', '2', '3', '4'
    ],
    correctB: [
        '0', '2', '1', '3', '4'
    ]

};

function shuffleArray(array) {
    let i = array.length - 1;
    for (; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        const temp = array[i];
        array[i] = array[j];
        array[j] = temp;
    }
    return array;
};

cards.cardId = shuffleArray(cards.cardId);

class GameField extends Component {
    constructor(props) {
        super(props);
        this.state = {
            cards: [
                {
                    droppableId: '0',
                    cardsArray: cards
                },
                {
                    droppableId: '1',
                    cardsArray: {
                        cardId: '',
                        cardImage: cards.cardImage,
                        correct: cards.correct,
                        correctB: cards.correctB,

                    }

                },
                {
                    droppableId: '2',
                    cardsArray: {
                        cardId: '',
                        cardImage: cards.cardImage,
                        correct: cards.correct,
                        correctB: cards.correctB,

                    }

                },
                {
                    droppableId: '3',
                    cardsArray: {
                        cardId: '',
                        cardImage: cards.cardImage,
                        correct: cards.correct,
                        correctB: cards.correctB,

                    }

                },
                {
                    droppableId: '4',
                    cardsArray: {
                        cardId: '',
                        cardImage: cards.cardImage,
                        correct: cards.correct,
                        correctB: cards.correctB,

                    }

                },
                {
                    droppableId: '5',
                    cardsArray: {
                        cardId: '',
                        cardImage: cards.cardImage,
                        correct: cards.correct,
                        correctB: cards.correctB,

                    }

                }
            ]
        }
    }

    render() {

        const onDragStart = () => {
            this.props.handleClock();
        }

        const onDragEnd = (result) => {
            const { destination, source, draggableId } = result;

            if (!destination) {
                return;
            }

            if (
                destination.droppableId === source.droppableId &&
                destination.index === source.index
            ) {
                return;
            }


            let sourceId;

            if (
                source.droppableId === "00" || source.droppableId === "10" || source.droppableId === "20" || source.droppableId === "30" || source.droppableId === "40"
            ) {
                sourceId = 0;
            }
            else {
                sourceId = source.droppableId;
            }


            const start = this.state.cards[sourceId]
            const finish = this.state.cards[destination.droppableId];

            console.log(start)
            console.log(source.index)


            const startTaskIds = Array.from(start.cardsArray.cardId);
            startTaskIds.splice(source.index, 1);
            console.log(startTaskIds)
            const newStart = {
                ...start.cardsArray,
                cardId: startTaskIds
            };

            const finishTaskIds = Array.from(finish.cardsArray.cardId);
            const finishTask = finishTaskIds[0]
            console.log(finishTaskIds[0])
            finishTaskIds.splice(0, 1);
            finishTaskIds.splice(destination.index, 0, draggableId);
            startTaskIds.splice(source.index, 0, finishTask)
            const newFinish = {
                ...finish.cardsArray,
                cardId: finishTaskIds
            }

            const newState = {
                ...this.state,
                cards: {
                    ...this.state.cards,
                    [sourceId]: {
                        ...this.state.cards[source.droppableId],
                        cardsArray: newStart
                    },
                    [destination.droppableId]: {
                        ...this.state.cards[destination.droppableId],
                        cardsArray: newFinish
                    }

                }
            }

            this.setState(newState)

            let correct = 0;

            for (let i = 0; i < 5; i++) {
                if (newState.cards[i + 1].cardsArray.cardId[0] === cards.correct[i] || newState.cards[i + 1].cardsArray.cardId[0] === cards.correctB[i]) {
                    console.log('zgadza sie')
                    correct++;
                }
                else if (newState.cards[i + 1].cardsArray.cardId[0] && (newState.cards[i + 1].cardsArray.cardId[0] !== cards.correct[i] || newState.cards[i + 1].cardsArray.cardId[0] !== cards.correctB[i])) {
                    this.props.handleClockDelay(true);
                }
            }

            if (correct === 5) {
                this.props.handleStop();
            }

            console.log(newState)

        };


        return (
            <DragDropContext onDragEnd={onDragEnd} onDragStart={onDragStart}>
                <div
                    className={`${"container"} ${"spacing"}`}
                >
                    {this.state.cards[0].cardsArray.cardId.map((id, index) => <Card key={id} id={id} droppableId={this.state.cards[0].cardsArray.correct[index] + '0'} image={this.state.cards[0].cardsArray.cardImage[id]} index={index} dropDisabled={true} />)}
                </div>
                <div className={`${"container"} ${"spacing"}`}>
                    <h5>... and drop them here to make the logo great again</h5>
                </div>
                <div className="container">
                    <Card key={this.state.cards[1].cardsArray.cardId[0]} id={this.state.cards[1].cardsArray.cardId[0]} droppableId={'1'} image={this.state.cards[0].cardsArray.cardImage[this.state.cards[1].cardsArray.cardId]} index={0} />
                    <Card key={this.state.cards[2].cardsArray.cardId[0]} id={this.state.cards[2].cardsArray.cardId[0]} droppableId={'2'} image={this.state.cards[0].cardsArray.cardImage[this.state.cards[2].cardsArray.cardId]} index={0} />
                    <Card key={this.state.cards[3].cardsArray.cardId[0]} id={this.state.cards[3].cardsArray.cardId[0]} droppableId={'3'} image={this.state.cards[0].cardsArray.cardImage[this.state.cards[3].cardsArray.cardId]} index={0} />
                    <Card key={this.state.cards[4].cardsArray.cardId[0]} id={this.state.cards[4].cardsArray.cardId[0]} droppableId={'4'} image={this.state.cards[0].cardsArray.cardImage[this.state.cards[4].cardsArray.cardId]} index={0} />
                    <Card key={this.state.cards[5].cardsArray.cardId[0]} id={this.state.cards[5].cardsArray.cardId[0]} droppableId={'5'} image={this.state.cards[0].cardsArray.cardImage[this.state.cards[5].cardsArray.cardId]} index={0} />
                </div>


            </DragDropContext >
        );
    }
}

export default GameField;