import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';


const Welcome = (props) => {

    const [name, setName] = useState('');
    const [error, setError] = useState(false);

    const history = useHistory();

    const handleChange = (event) => {
        setName(event.target.value);
    }

    const handleSubmit = (e) => {
        if(name === ''){
            setError(true);
        }
        else{
            props.changeName(name)
            history.push('/game');
        }
        e.preventDefault();
    }

    const handleDismiss = () => {

        setError(false)
    }
    

    return (
        <div className="welcomeView">
            <h1>Hello friend, tell me your name...</h1>
            <form onSubmit={handleSubmit}>
                <input className='nameForm' type="text" placeholder="Your name here" value={props.name} onChange={handleChange} /><br></br>
                <button className="btn btn-light" type="submit">Let's go -{'>'}</button>
            </form>
            {error === true &&
            <p className="alert alert-danger alert-dismissible" role="alert">
            Name was not entered!
            <button type="button" className="close" data-dismiss="alert" aria-label="Close" onClick={handleDismiss}>
                <span aria-hidden="true">&times;</span>
            </button>
            </p>
            }
            
        </div>
    );
}

export default Welcome;