import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faClock } from '@fortawesome/free-regular-svg-icons'

class Clock extends Component {
    constructor(props) {
        super(props);
        this.state = {
            clockStarted: false,
        }
    }




    componentDidUpdate() {
        if (this.props.delay === true) {
            this.props.addScore(10)
            this.props.delayHappened();
        }
        if (this.props.clockState === true && this.state.clockStarted === false) {
            this.setState({ clockStarted: true })
            let myInterval = null;
            this.props.addScore(0.5);
            myInterval = setInterval(() => {
                this.props.addScore(0.5);
                if (this.props.clockState === false) {
                    clearInterval(myInterval);
                }
            }, 500);
            return () => clearInterval(myInterval);
        }
    };

    render() {

        return (
            <React.Fragment>
                <FontAwesomeIcon icon={faClock} size="lg" color="#49f276" className="clock"/>
                <h2>Your score: {Math.round(this.props.score)} seconds</h2>
            </React.Fragment>
        );
    }
}

export default Clock;