import React, { Component } from 'react';
import GameField from "./gameField"
import Clock from "./clock"
import WinScreen from "./winScreen"


class Game extends Component {
    constructor(props) {
        super(props);
        this.state = {
            clockStarted: false,
            delay: false,
            win: false,
            score: 0
        }
    }


    render() {

        const setState = () => {
            this.setState({ clockStarted: true })
        }

        const stopClock = () => {
            this.setState({ clockStarted: false })
            this.setState({win: true})

        }

        const handleClockDelay = (state) => {
            this.setState({ delay: state })
        }

        const delayHappened = () => {
            this.setState({delay: false})
        }

        const handleScore = (score) => {
            this.setState({score: this.state.score + score})
        }


        return (
            <div className='main'>

            {this.state.win === true && 
                <WinScreen score={this.state.score} />
            }
                <div className={`${"container"}`} style={{paddingTop: '5%'}}>
                    <h1>Good luck, {this.props.name}!</h1>
                    <Clock clockState={this.state.clockStarted} delay={this.state.delay} score={this.state.score} delayHappened={delayHappened} addScore={handleScore} />
                    <div className="break"></div>
                    <h5>Pick up the right cards</h5>
                    <h5>The faster the better!</h5>
                </div>
                <GameField handleClock={setState} handleStop={stopClock} handleClockDelay={handleClockDelay} />

            </div>

        );
    }

}

export default Game;